<template>
  <div class="hello">
    <input v-model="msg">
    <h1 v-bind:class="{small:flag}">{{ msg }}</h1>
    让他们归于尘土：<input type='checkbox' v-model="flag">

  </div>
</template>

<script>
export default {
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      flag: false
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.small{
  display: none;
}
</style>



